@use 'styles/colors';
@import 'styles/base';

::ng-deep {
  .admin-payments-dialog > .mat-mdc-dialog-container > .mdc-dialog__container > .mat-mdc-dialog-surface {
    background-color: transparent !important;
  }

  .admin-payments-dialog-backdrop {
    background-color: rgba(0, 0, 0, 0.8) !important;
  }
}

:host {
  display: flex;
  flex-direction: column;
  height: 100vh;

  .header {
    padding: 10px 20px;

    //position: sticky;
    //top: 0;
    background-color: white;
    border-bottom: 2px solid gainsboro;
    box-shadow: 0 0 40px 0 #080808;
    box-sizing: border-box;
    z-index: 1;

    display: flex;
    align-items: center;
    justify-content: space-between;

    section {
      display: flex;
      align-items: center;
    }

    .title {
      @include proxima(21px, 700, colors.$foreground_dark);

      a {
        @include proxima(21px, 700, colors.$foreground_light-dark);
        border-bottom: 1px dotted #616161;
        opacity: 0.8;
        text-decoration: none;
      }
    }

    .sorting-select {
      width: 280px;

      ::ng-deep {
        .mat-mdc-form-field-subscript-wrapper {
          display: none !important;
        }
      }
    }

    .close-button {
      padding: 20px;
      opacity: 0.8;
      cursor: pointer;

      &:hover {
        opacity: 1;
      }
    }
  }

  .content {
    width: 100vw;
    flex-grow: 1;
    display: flex;
    align-items: baseline;
    justify-content: center;
    overflow-x: hidden;
    position: relative;

    .feed {
      min-width: 100vw;
      display: flex;
      gap: 100px;
      position: relative;
      //transition: opacity 1500ms ease-in-out;
    }

    .payment-block {
      display: flex;
      align-items: center;
      transition: all 500ms ease-in-out;

      transform: scale(0.8);

      &__inner {
        display: flex;
        align-items: center;
        opacity: 0.5;
        transition: all 500ms ease-in-out;
      }

      .button {
        display: flex;
        align-items: center;
        height: 100%;
        padding: 20px;
        opacity: 0;
        cursor: pointer;
        pointer-events: none;
        transition: opacity 150ms ease-in-out;

        .icon {
          background-color: #FFFFFF;
        }
      }

      &.active {
        transform: scale(1);

        .payment-block__inner {
          opacity: 1;
        }

        .button {
          opacity: 0.8;
          pointer-events: all;

          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }

  @media screen and (max-width: 700px) {
    .header {
      flex-direction: column;

      .title {
        margin-top: 5px;
        margin-bottom: 10px;
      }

      section {
        width: 100%;

        &:first-child {
          justify-content: center;
        }

        &:last-child {
          justify-content: space-between;
        }
      }
    }
  }

  @media screen and (max-width: 340px) {
    .header {
      .sorting-select {
        width: auto !important;
        min-width: unset;
      }
    }
  }
}
